var Common = require("../common");
var httpsPOST = Common.httpsPOST;
var Conf = require('../conf');

/**
 * 创建成员分组,供业务方面与业务相关分组挂钩,进行人员同步变动
 * @param members:Array[Number]
 * **/
exports.createMemberGroup = function(members) {
    var data = {};
    data.members = members;
    return httpsPOST({
        hostname: Conf.BACKEND.HOST,
        port: Conf.BACKEND.PORT,
        path: Conf.PREFIX+'/memberGroup/createMemberGroup',
    }, data);
};

/**
 * 批量添加成员
 * @param groupId:Number  成员组ID
 * @param members:Array[Number]
 * **/
exports.addMember = function(groupId, members) {
    var data = {};
    data.groupId = groupId;
    data.members = members;
    return httpsPOST({
        hostname: Conf.BACKEND.HOST,
        port: Conf.BACKEND.PORT,
        path: Conf.PREFIX+'/memberGroup/addMember',
    }, data);
};

/**
 * 批量删除成员
 * @param groupId:Number 成员组ID
 * @param members:Array[Number]
 * **/
exports.removeMember = function(groupId, members) {
    var data = {};
    data.groupId = groupId;
    data.members = members;
    return httpsPOST({
        hostname: Conf.BACKEND.HOST,
        port: Conf.BACKEND.PORT,
        path: Conf.PREFIX+'/memberGroup/removeMember',
    }, data);
};




