var Common = require("../common");
var httpsPOST = Common.httpsPOST;
var Conf = require('../conf');

/**
 * 创建会话房间
 * @param chatGroup:Object
 * {
 *   name:String  房间名
 *   publicType:Number 房间公开性
 *   topicType:Number 主题类型
 *   topicId:Number 主题Id
 *   ownerRoleId:Number 创建者id
 *   members:[{memberType:Number,memberId:String}]
 * }
 * **/
exports.createChatGroup = function (chatGroup) {
    var data = {};
    data.chatGroup = chatGroup;
    return httpsPOST({
        hostname: Conf.BACKEND.HOST,
        port: Conf.BACKEND.PORT,
        path: Conf.PREFIX+'/chatGroup/createChatGroup',
        }, data);
}

/**
 * 批量添加会话成员
 * @param groupId:Number  房间id
 * @param members:[{memberType:Number,memberId:String}]
 * **/
exports.addChatGroupMember = function(groupId, members) {
    var data = {};
    data.groupId = groupId;
    data.members = members;
    return httpsPOST({
        hostname: Conf.BACKEND.HOST,
        port: Conf.BACKEND.PORT,
        path: Conf.PREFIX+'/chatGroup/addChatGroupMember',
    }, data);
};

/**
 * 批量移除会话成员
 * @param groupId:Number  房间id
 * @param members:[{memberType:Number,memberId:Number}]
 * **/
exports.removeChatGroupMember = function(groupId, members) {
    var data = {};
    data.groupId = groupId;
    data.members = members;
    return httpsPOST({
        hostname: Conf.BACKEND.HOST,
        port: Conf.BACKEND.PORT,
        path: Conf.PREFIX+'/chatGroup/removeChatGroupMember',
    }, data);
};

/**
 * 设置会话房间名称
 * @param groupId:Number 成员组ID
 * @param name:String 会话房间名称
 * **/
exports.updateChatGroupName = function(groupId, name) {
    var data = {};
    data.groupId = groupId;
    data.name = name;
    return httpsPOST({
        hostname: Conf.BACKEND.HOST,
        port: Conf.BACKEND.PORT,
        path: Conf.PREFIX+'/chatGroup/updateChatGroupName',
    }, data);
};

/**
 * 设置会话房间公开性
 * @param req.body.groupId:Number 成员组ID
 * @param req.body.publicType:Number 会话房间公开性
 * **/
exports.updatePublicType = function(groupId, publicType) {
    var data = {};
    data.groupId = groupId;
    data.publicType = publicType;
    return httpsPOST({
        hostname: Conf.BACKEND.HOST,
        port: Conf.BACKEND.PORT,
        path: Conf.PREFIX+'/chatGroup/updatePublicType',
    }, data);
};


