var Simple = require("./protobuf");
const c2c = {
	'message msg': {
		'optional string _id': 1,
		'optional uInt32 type': 2, //消息类型
		'optional uInt32 fromUserId': 3,//发送者userId
		'optional uInt32 toUserId': 4,//接收者userId
		'optional uInt32 groupId': 5,//相关id,比如讨论组Id,合同Id,发布Id等
		'optional uInt32 affairId': 6,//事务Id
		'optional uInt32 toRoleId': 7,//接收者roleId
		'optional uInt32 fromRoleId': 8,//发送者roleId
		'optional uInt32 sub': 9,//消息子类型
		'optional string name': 10,//发送者名称
		'optional string content': 11,//发送内容
		'optional uInt32 time': 12,//发送时间,不需要填
		'optional uInt32 index': 13,//接收消息的时候会有index做验证
        'optional string apns': 14,//@某人的roleId列表 json
        'optional string _key': 15,//聊天唯一标志
        'optional uInt32 state': 16//聊天消息状态
	},

	'message c2c': {
		'optional uInt32 type': 1,
		'optional string requestId': 2,
		'optional msg chat': 5,//附带的聊天相关消息
		'optional string params': 3,//常用于请求中携带参数,可以是json
		'repeated msg msgList': 4,//返回消息体
		'optional string data': 6,//返回的普通消息体，可以是string或者json.stringify之后的
	}
}

var protos = Simple.parse(c2c);
Simple.init({encoderProtos: protos, decoderProtos: protos});

exports.encodeChat = function (o) {
	return Simple.encode("message msg", o);
}


exports.decodeChat = function (buff) {
	return Simple.decode("message msg", buff);
}

exports.encodeC2c = function (o) {
	return Simple.encode("message c2c", o);
}

exports.decodeC2c = function (buff) {
	return Simple.decode("message c2c", buff);
}