//service
exports.groupChatService = require('./service/groupChatService');
exports.privateChatService = require('./service/privateChatService');
exports.roomManagementService = require('./service/roomManagementService');
exports.memberGroupService = require('./service/memberGroupService');
exports.serverService = require('./service/serverService');
exports.notifyService = require('./service/notifyService');

exports.Constants = require('./constants');
Conf = require('./conf');

var client_core = require('./client-core');

/**
 *
 * @param address 连接地址
 * @param userId userId
 * @param token  验证token
 * @param handler {onMsg:监听别人发送消息，包括自己的其他端发送的消息, onSign:监听成功登陆,onBumped:监听被其他web挤掉,
 * onNotice:监听系统推送消息，onNewLogin：监听其他客户端新登录信息}
 * @param config  配置{pingInterval:3*60*1000,requestTimeout:2000,debug:true}
 */

exports.setServerAddress = function (host, port) {
    Conf.BACKEND.HOST = host;
    Conf.BACKEND.PORT = port;
}
exports.start = client_core.init;
exports.signOut = client_core.signOut;
exports.addOnceHandler = client_core.addOnceHandler;
exports.addHandler = client_core.addHandler;
exports.removeHandler = client_core.removeHandler;